import React from "react";
import Rellax from "rellax";

import "./Projects.css";
import octocat from "./pics/projects/octocat.png";

import trees from "./pics/wallpaper/trees.png";
import star from "./pics/wallpaper/star.png";
import land from "./pics/wallpaper/land.png";
import moon from "./pics/wallpaper/moon.svg";
import contents from "./pics/projects/contents.png";

class Projects extends React.Component {
  projects = [
    {
      title: "Clock",
      des:
        "Small analog clocks' arms align to form the current time. Time is based on 12 hours format.",
      github: "https://github.com/winjitn/wallclock",
      color: "#f76160",
      shortdes: "Digital clock made from analog clocks",
      path: "clock"
    },
    {
      title: "Chatroom",
      des:
        "Chat built with socket io allowing custom namespace with ability handle multiple sockets.",
      github: "https://github.com/winjitn/io-chat",
      color: "#21364b",
      shortdes: "Chatroom with namespace parameter",
      path: "chatio"
    },
    {
      title: "Validate Form",
      des:
        "Validation checks on submission and displays insufficiencies with messages and mark corresponding fields. Form inputs styled with semantic-ui library.",
      github: "https://github.com/winjitn/form-validate",
      color: "#f8c687",
      shortdes: "Custom form validation",
      path: "validate"
    },
    {
      title: "Typing animation",
      des: "Animated example of query format and result.",
      github: "https://github.com/winjitn/query-anim",
      color: "#679895",
      shortdes: "Animated query result example",
      path: "typinganim"
    },
    {
      title: "FX rate",
      des:
        "Scraped foreign exchange rates of USD, JPY, and KRW from Supperrich and other major banks with puppeteer and cheerio. Rates are not adjusted and is denominated differently.",
      github: "https://github.com/winjitn/fx-diff",
      color: "#7dcc93",
      shortdes: "Shows forex rates from some major banks",
      path: "fx"
    },
    {
      title: "Infinite scrolling",
      des:
        "Load more content when scrolling hits bottom of the page to save memory and allow faster page startup.",
      github: "https://github.com/winjitn/infinite-scroll",
      color: "#E29470",
      shortdes: "Load more content as you go",
      path: "infinitescroll"
    },
    {
      title: "Parallax",
      des:
        "Parallax effect with varying scroll speed to give depth to the content.",
      github: "https://github.com/winjitn/parallax",
      color: "#66A7D5",
      shortdes: "Parallax webpage",
      path: "parallax"
    },
    {
      title: "Typing test",
      des:
        "Typing test with randomized excerpt that shows typing speed in WPM upon completion.",
      github: "https://github.com/winjitn/typing-test",
      color: "#21364b",
      shortdes: "English speed typing test",
      path: "typingtest"
    },
    {
      title: "Lazy loading",
      des:
        "Lazing loading with content shimmer as placeholder for affected elements.",
      github: "https://github.com/winjitn/lazy-shimmer",
      color: "#40a18c",
      shortdes: "Loads element when it enters view",
      path: "lazyshimmer"
    }
  ];

  tally = 0;

  componentDidMount() {
    document.title = "Projects ● Pakorn Jitngamplang - Front End Developer";

    window.scrollTo(0, 0);

    var rellax = new Rellax(".rellax");
  }

  componentWillUnmount() {
    document.querySelector("html").style.scrollBehavior = "auto";
  }

  load() {
    this.tally += 1;
    //2 projects
    if (this.tally === 32) {
      document.querySelector("html").style.scrollBehavior = "smooth";

      document.querySelector(".projects-ctn").style.opacity = "1";
      var io = new IntersectionObserver(
        entries => {
          this.gifToggle(entries[0].target, false);
        },
        {
          threshold: [0.8]
        }
      );

      const projectImg = document.querySelectorAll(".project-img");
      for (var i = 0; i < projectImg.length; i++) {
        io.observe(projectImg[i]);
      }
    }
  }

  gifToggle(e, action = true) {
    const on = e.getAttribute("on");
    const playButton = e.parentElement;
    if (action === true) {
      if (on === "false") playButton.classList.remove("play");
      else playButton.classList.add("play");
      e.setAttribute(
        "src",
        on === "false"
          ? require("" + e.getAttribute("gif"))
          : require("" + e.getAttribute("img"))
      );
      e.setAttribute("on", on === "false" ? "true" : "false");
    } else {
      if (on !== "false") {
        playButton.classList.add("play");
        e.setAttribute("src", require("" + e.getAttribute("img")));
        e.setAttribute("on", "false");
      }
    }
  }

  table() {
    const tablebody = this.projects.map(project => (
      <tr key={project.title}>
        <td style={{ width: "50%" }}>
          <h4 className="ui image header">
            <a href={`${project.github}`} className="linkctn">
              <img
                src={octocat}
                alt="github"
                className="ui mini rounded image"
              />
            </a>
            <div className="content">
              <a href={`#${project.title}`}>{project.title}</a>
            </div>
          </h4>
        </td>
        <td style={{ verticalAlign: "middle" }}>{project.shortdes}</td>
      </tr>
    ));
    return (
      <div className="table-height-wrapper">
        <table className="ui collapsing very basic celled table">
          <thead>
            <tr>
              <th>Name</th>
              <th>Notes</th>
            </tr>
          </thead>
          <tbody>{tablebody}</tbody>
        </table>
      </div>
    );
  }

  renderProjects() {
    return this.projects.map(project => (
      <div
        className="project-ctn"
        style={{ backgroundColor: `${project.color}` }}
        key={project.title}
        id={project.title}
      >
        <div className="ui container">
          <div
            className={`project-img-wrapper play ${
              project.title === "FX rate" ? "static" : null
            }`}
            onClick={e => this.gifToggle(e.target.firstChild || e.target)}
          >
            <img
              className="project-img"
              alt="project"
              src={require(`./pics/projects/content/${project.path}.png`)}
              gif={`./pics/projects/content/${project.path}.${
                project.title === "FX rate" ? "png" : "gif"
              }`}
              img={`./pics/projects/content/${project.path}.png`}
              on="false"
            />
          </div>
          <div className="project-title">{project.title}</div>
          <div className="project-des">{project.des}</div>
          <a href={project.github}>
            <img className="project-github" alt="github" src={octocat} />
          </a>
        </div>
      </div>
    ));
  }

  render() {
    return (
      <div className="projects-ctn" onLoad={() => this.load()} id="top">
        <a href="#top">
          <div className="jumptop">
            <i className="hand point up icon big" />
          </div>
        </a>
        <div className="wallpaper-wrapper-project">
          <div className="table-wrapper">
            <div className="table">
              <img className="table-content" src={contents} alt="contents" />
              <div className="table-content-extend"></div>
              {this.table()}
            </div>
          </div>
          <div className="wallpaper-project nightsky"></div>
          <img className="wallpaper-project " alt="wallpaper" src={star} />
          <img
            className="moon-project rellax"
            data-rellax-speed="-5"
            alt="wallpaper"
            src={moon}
          />
          <img
            className="wallpaper-project rellax"
            data-rellax-speed="-1"
            alt="wallpaper"
            src={land}
          />
          <div className="wallpaper-back rellax" data-rellax-speed="3"></div>
          <img
            className="wallpaper-project rellax"
            data-rellax-speed="6"
            alt="wallpaper"
            src={trees}
          />
        </div>
        <div>{this.renderProjects()}</div>
      </div>
    );
  }
}

export default Projects;
