import React from "react";
import Rellax from "rellax";

import "./Home.css";
import profile from "./pics/me.png";

import es6 from "./pics/logo/es6.svg";
import express from "./pics/logo/express.svg";
import html from "./pics/logo/html.svg";
import mongodb from "./pics/logo/mongodb.svg";
import nodejs from "./pics/logo/nodejs.svg";
import react from "./pics/logo/react.svg";
import css3 from "./pics/logo/css3.svg";

import isowall from "./pics/home/isowall.png";
import wreathleft from "./pics/home/wreathleft.svg";
import wreathright from "./pics/home/wreathright.svg";
import mail from "./pics/home/mail.svg";

class Home extends React.Component {
  logos = [react, es6, css3, html, nodejs, express, mongodb, react];
  logosRef = React.createRef();
  tally = 0;

  componentDidMount() {
    document.title = "Pakorn Jitngamplang - Front End Developer";

    var rellax = new Rellax(".rellax");

    requestAnimationFrame(() => {
      window.scrollTo(0, 0);
    });

    const obj = this;

    const logos = this.logosRef.current;
    obj.logoSlide(logos);
    setTimeout(() => {
      logos.style.transition = "none";
      logos.style.transform = "translateY(0)";
    }, 22.1e3);
    this.id = setInterval(() => {
      obj.logoSlide(logos);
      setTimeout(() => {
        logos.style.transition = "none";
        logos.style.transform = "translateY(0)";
      }, 22.1e3);
    }, 22.2e3);
  }

  componentWillUnmount() {
    clearInterval(this.id);
  }

  logoSlide(logos) {
    for (var i = 1; i < this.logos.length; i++) {
      this.logoSlideAnim(i, logos);
    }
  }

  logoSlideAnim(i, e) {
    setTimeout(() => {
      e.style.transition = "1s cubic-bezier(0.74, 0, 0.36, 0.99)";
      e.style.transform = `translateY(-${i * (100 / 8)}%)`;
    }, i * 3e3);
  }

  renderLogo() {
    return this.logos.map(logo => (
      <img src={logo} className="logo" alt="logo" />
    ));
  }

  loadCount() {
    this.tally += 1;
    if (this.tally >= 13) {
      document.querySelector(".home-ctn").style.opacity = "1";
    }
  }
  render() {
    return (
      <div className="home-ctn" onLoad={() => this.loadCount()}>
        <div className="wallpaper-wrapper">
          <img className="wallpaper iso" alt="wallpaper" src={isowall} />
          <div className="name rellax" data-rellax-speed="-4">
            pj
          </div>
        </div>
        <div
          className="ui container overflow-contain"
          style={{
            marginTop: "-50px"
          }}
        >
          <div className="body">
            <div className="img-ctn-one">
              <img id="body-img" src={profile} alt="p" />
              <img id="wreathleft" src={wreathleft} alt="wreath" />
              <img id="wreathright" src={wreathright} alt="wreath" />
            </div>
            <div id="body-text">
              <div id="hi" style={{ color: "#1b7f69" }}>
                Hi.
              </div>
              Thanks for dropping by! I am a self taught web developer
              specializing in front end development. I love figuring out how new
              designs are implemented and have a passion for puzzle solving.
            </div>
            <div className="divider">
              <div className="dot"></div>
              <div className="dot"></div>
              <div className="dot"></div>
              <div className="dot"></div>
              <div className="dot"></div>
              <div className="dot"></div>
              <div className="dot"></div>
            </div>
            <div className="img-ctn-one">
              <div id="browser"></div>
              <div id="browser-dot">
                <div id="dot-red"></div>
                <div id="dot-yellow"></div>
                <div id="dot-green"></div>
                <div id="address-bar"></div>
              </div>
              <div id="body-img-ctn">
                <div id="logo" ref={this.logosRef}>
                  {this.renderLogo()}
                </div>
              </div>
            </div>
            <div id="body-text">
              <div id="hi">What I can do.</div>I generally work with React for
              the client side, Express/Node for the server side if required, and
              MongoDB for database needs to complete the stack.
            </div>
            <div className="btn-wrapper">
              <span className="speech-bubble">
                <img src={mail} id="mail" alt="mail" />
              </span>
              <button className="ui dgreen large button">
                <i className="envelope icon"></i>
                <a className="mail" href="mailto:winjitn@gmail.com">
                  Contact Me
                </a>
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Home;
