import React from "react";

import four from "./pics/404/four.svg";
import fist from "./pics/404/fist.svg";

import styles from "./Error.module.css";

export default () => {
  return (
    <div className={styles.errorwrapper}>
      <div className={styles.ctn}>
        <img className={styles.fourone} src={four} alt="error" />
        <img className={styles.fist} src={fist} alt="error" />
        <img className={styles.fourone} src={four} alt="error" />
      </div>
    </div>
  );
};
