import React from "react";
import { NavLink, Link } from "react-router-dom";

import profile from "./pics/me.png";
import "./NavBar.css";

class NavBar extends React.Component {
  componentDidMount() {
    window.addEventListener("scroll", () => {
      this.scroll();
    });
  }

  scroll() {
    const nav = document.querySelector(".nav");
    const navele = document.querySelector(".navelement");
    if (window.scrollY >= window.innerHeight * 0.4) {
      nav.classList.add("nav-active");
      navele.classList.add("navele-active");
    } else {
      nav.classList.remove("nav-active");
      navele.classList.remove("navele-active");
    }
  }
  render() {
    return (
      <div className="ui container nav">
        <div className="navbar">
          <Link to="/">
            <img id="profile" src={profile} alt="p" />
          </Link>
          <ul className="navelement">
            <li>
              <NavLink
                exact
                activeClassName="link-active"
                className="link"
                to="/"
              >
                Home
              </NavLink>
            </li>
            <li>
              <NavLink
                exact
                activeClassName="link-active"
                className="link"
                to="/projects"
              >
                Projects
              </NavLink>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}

export default NavBar;
